import * as constants from './constants';

const initRecommendInfo = {
  type: "0",
  listCode: ""
};

const defaultState = {
  recommendList: [],
  domain_name: "",
  recommendModal: false,
  recommendInfo: initRecommendInfo
};

export default (state = defaultState, action) => {
  let newState = JSON.parse(JSON.stringify(state));
  switch (action.type) {
    case constants.GET_RECOMMEND_LIST:
      newState.recommendList = [...action.recommendList];
      return newState;
    case constants.GET_DOMAIN_NAME:
      newState.domain_name = action.domain_name;
      return newState;
    case constants.DELETE_ITEM:
      newState.recommendList.splice(action.index, 1)
      return newState;
    case constants.OPEN_RECOMMEND_MODAL:
      newState.recommendModal = true;
      return newState;
    case constants.CLOSE_RECOMMEND_MODAL:
      newState.recommendInfo = initRecommendInfo;
      newState.recommendModal = false;
      return newState;
    // case constants.CREATE_RECOMMEND_LIST:


    case constants.RESET_STATE:
      newState = defaultState;
      return newState;
    default:
      return state;
  }
};