import React, {Component} from 'react';
import {Table, Input, Button, Modal, message} from "antd";
import BreadCrumb from '../../../common/bread';
import axios from 'axios';

const {Search} = Input;

class novelSpread extends Component {
  constructor(props) {
    super(props);
    this.changeSearchUrl = this.changeSearchUrl.bind(this);
    this.changeReplaceUrl = this.changeReplaceUrl.bind(this);
    this.searchUrl = this.searchUrl.bind(this);
    this.updateReplaceUrl = this.updateReplaceUrl.bind(this);
    this.closeSpreadModal = this.closeSpreadModal.bind(this);
  }

  state = {
    replaceUrl: "",
    spreadData: [],
    spreadModal: false,
    searchUrl: "",
    _id: ""
  };

  render() {
    const {replaceUrl, spreadData, spreadModal, searchUrl} = this.state;
    const spreadHeader = [
      {
        dataIndex: "_id",
        key: "_id",
        title: "id",
        align: "center",
      },
      {
        dataIndex: "url",
        key: "url",
        title: "url",
        align: "center",
        width: 300,
        render: text => <p style={{wordWrap: "break-word", width: 300}}>{text}</p>
      },
      {
        dataIndex: "replaceUrl",
        key: "replaceUrl",
        title: "replaceUrl",
        align: "center",
        width: 300,
        render: text => <p style={{wordWrap: "break-word", width: 300}}>{text}</p>
      },
      {
        key: "Action",
        title: "Action",
        align: "center",
        render: (text, record) => <Button size="small"
                                                 onClick={() => this.openSpreadModal(record._id)}>修改replaceUrl</Button>
      }
    ];
    return (
      <div>
        <BreadCrumb/>
        <div className="container">
          <Search
            style={{width: 300, marginBottom: 20}}
            placeholder="请输入查询链接"
            value={searchUrl}
            enterButton="搜索"
            onSearch={this.searchUrl}
            onChange={this.changeSearchUrl}
          />
          <Table rowKey={record => record._id} bordered size="small" columns={spreadHeader} dataSource={spreadData}/>
        </div>
        <Modal
          visible={spreadModal}
          title="请输入replaceUrl"
          onOk={this.updateReplaceUrl}
          onCancel={this.closeSpreadModal}
        >
          <Input value={replaceUrl} placeholder="请输入replaceUrl" onChange={this.changeReplaceUrl}/>
        </Modal>
      </div>
    )
  }

  componentDidMount() {
    this.getSpreadData()
  }

  getSpreadData() {
    axios.get("/novel_transfer").then(res => {
      let spreadData = res.data.data;
      this.setState({
        spreadData
      })
    });
  }

  openSpreadModal(_id) {
    this.setState({
      spreadModal: true,
      _id
    })
  }

  updateReplaceUrl() {
    const {_id, replaceUrl} = this.state;
    axios.get('/novel_transfer/update', {
      params: {_id, replaceUrl}
    }).then(res => {
      message.info(res.data.success);
      this.closeSpreadModal();
      this.getSpreadData();
    })
  }

  closeSpreadModal() {
    this.setState({
      spreadModal: false,
      replaceUrl: ""
    })
  }

  searchUrl() {
    axios.get("/novel_transfer/find_one", {
      params: {searchUrl: this.state.searchUrl}
    }).then(res => {
      if (res.data.data.length > 0) {
        message.info(res.data.success);
        let spreadData = res.data.data;
        this.setState({
          spreadData,
          searchUrl: ""
        })
      } else {
        message.info(res.data.error);
      }
    });
  }

  changeSearchUrl(e) {
    this.setState({
      searchUrl: e.target.value
    })
  }

  changeReplaceUrl(e) {
    this.setState({
      replaceUrl: e.target.value
    })
  }
}

export default novelSpread;