import React, {Component} from 'react';
import "./index.scss";
import {Input, Icon, message} from 'antd';

class Login extends Component {
  constructor(props) {
    super(props);
    this.login = this.login.bind(this);
    this.state = {
      loginInfo: {
        username: "",
        password: ""
      },
      redirect_url: ""
    }
  }

  componentDidMount() {
    const { search } = window.location;
    const redirect_url = search ? decodeURIComponent(search.split("=")[1]) : "/admin";
    this.setState({redirect_url});
  }

  render() {
    const { loginInfo } = this.state;
    const bgStyle = {
      minHeight: "100vh",
      backgroundSize: "100%",
      backgroundRepeat: "no-repeat",
      backgroundAttachment: "fixed",
      backgroundImage: `url(${require("../../assets/images/login_bg.png")})`
    };
    return (
      <div className="login-bg" style={bgStyle}>
        <div className="bg-mask"></div>
        <div className="content-wrapper">
          <div className="content">
            <p className="title">明星说后台管理系统</p>
            <p>运营与管控一体化的整合式服务平台</p>
            <p>一起解构数字世界，碰撞科技创新思想</p>
          </div>
        </div>
        <div className="login-form-wrapper">
          <div className="login-form">
            <h4 className="login-title">登录</h4>
            <Input
              value={loginInfo.username}
              size="large"
              onChange={e => this.changeInputValue(e.target.value, "username")}
              prefix={<Icon type="user" style={{color: 'rgba(0,0,0,.25)', marginRight: 10}}/>}
              style={{marginBottom: 30}}
              placeholder="用户名"
            />
            <Input
              value={loginInfo.password}
              size="large"
              type="password"
              onChange={e => this.changeInputValue(e.target.value, "password")}
              prefix={<Icon type="lock" style={{color: 'rgba(0,0,0,.25)', marginRight: 10}}/>}
              placeholder="密码"
            />
            <div className="login-btn cursor" onClick={this.login}>登录</div>
          </div>
        </div>
      </div>
    )
  }

  changeInputValue(value, name) {
    const { loginInfo } = this.state;
    loginInfo[name] = value;
    this.setState(state => ({loginInfo: {...state.loginInfo, ...loginInfo}}))
  }

  login() {
    const { loginInfo, redirect_url } = this.state;
    if(!loginInfo.username || !loginInfo.password || (loginInfo.username !== "mingxingshuo" && loginInfo.password !== "mingxingshuo123")) return message.error("用户名或密码输入有误");
    loginInfo.date = new Date().getTime();
    const userInfo = JSON.stringify(loginInfo);
    localStorage.setItem("userInfo", userInfo);
    window.location.href = redirect_url;
    this.setState({loginInfo: {}, redirect_url: ""});
    message.success("登录成功")
  }
}

export default Login;