import React, { Component } from 'react';
import { Input, Button, Table, Modal, message } from 'antd';
import axios from 'axios';
import BreadCrumb from '../../../common/bread';

class Qiangguan extends Component {
  constructor(props) {
    super(props);
    this.submitQiangguan = this.submitQiangguan.bind(this);
  }
  state = {
    qiangguanModal: false,
    qiangguanList: [],
    qiangguanInfo: {
      jumpLink: "",
      wechatId: ""
    }
  };
  render() {
    const { qiangguanList, qiangguanModal, qiangguanInfo } = this.state;
    const qiangguanHeader = [
      {
        title: "id",
        dataIndex: "id",
        key: "id",
        align: "center"
      },
      {
        key: "jumpLink",
        dataIndex: "jumpLink",
        title: "跳转链接",
        align: "center"
      },
      {
        key: "wechatId",
        dataIndex: "wechatId",
        title: "微信原始ID",
        align: "center"
      },
      {
        title: "Action",
        align: "center",
        width: 160,
        render: (text, record, index) => <div>
          <Button size="small" type="primary" style={{marginRight: 10}} onClick={() => this.setState({qiangguanModal: true, qiangguanInfo: {...record}})}>修改</Button>
          <Button size="small" type="danger" onClick={() => this.deleteLink(record._id, index)}>删除</Button>
        </div>
      }
    ];
    return (
    	<div>
        <BreadCrumb />
        <div className="container">
          <Button style={{marginBottom: 20}} type="primary" onClick={() => this.setState({qiangguanModal: true})}>新增强关链接</Button>
          <Table rowKey={record => record._id} size="small" bordered dataSource={qiangguanList} columns={qiangguanHeader} />
        </div>
        <Modal
          visible={qiangguanModal}
          title="编辑强关链接"
          onOk={this.submitQiangguan}
          onCancel={() => this.setState({qiangguanModal: false, qiangguanInfo: {}})}
          okText="提交"
          cancelText="取消"
        >
          <Input.TextArea style={{marginBottom: 20}} value={qiangguanInfo.jumpLink} placeholder="请输入跳转链接" onChange={e => this.changeInputValue(e.target.value, "jumpLink")} />
          <Input value={qiangguanInfo.wechatId} placeholder="请输入微信原始ID" onChange={e => this.changeInputValue(e.target.value, "wechatId")} />
        </Modal>
    	</div>
    )
  }

  componentDidMount() {
    this.getQiangguanList();
  }

  getQiangguanList() {
    axios.get("/qiangguan").then(res => {
      if (res.data.success) {
        const { data: qiangguanList } = res.data;
        this.setState({qiangguanList})
      }
    });
  }

  changeInputValue(value, name) {
    const { qiangguanInfo } = this.state;
    qiangguanInfo[name] = value;
    this.setState(state => ({qiangguanInfo: {...state.qiangguanInfo, ...qiangguanInfo}}))
  }

  submitQiangguan() {
    const { qiangguanInfo } = this.state;
    let url = "/qiangguan/create";
    qiangguanInfo._id && (url = "/qiangguan/update");
    axios.post(url, qiangguanInfo).then(res => {
      if (res.data.success) {
        message.info(res.data.success);
        this.getQiangguanList();
        this.setState({qiangguanModal: false, qiangguanInfo: {}});
      } else {
        message.info(res.data.error);
      }
    });
  }

  deleteLink(_id, index) {
    const { qiangguanList } = this.state;
    Modal.confirm({
      title: "确定删除这条链接吗？",
      onOk: () => {
        axios.get("/qiangguan/del", { params: { _id }})
          .then(res => {
            if (res.data.success) {
              message.info(res.data.success);
              qiangguanList.splice(index, 1);
              this.setState({qiangguanList})
            } else {
              message.info(res.data.error);
            }
          });
      }
    })
  }

}

export default Qiangguan;