import React, { Component } from 'react';
import BreadCrumb from '../../../common/bread';
import { Table, Modal, message } from 'antd';
import axios from "axios";
import { Link } from 'react-router-dom';
import Button from '../../../common/components/Button';

class Advertise extends Component {
  state = {
    advertiseList: []
  };
  render() {
    const { advertiseList } = this.state;
    const advertiseHeader = [
      {
        key: "id",
        dataIndex: "id",
        title: "素材id",
        align: "center"
      },
      {
        key: "title",
        dataIndex: "title",
        title: "素材标题",
        align: "center"
      },
      {
        key: "novelLink",
        dataIndex: "novelLink",
        title: "小说链接",
        align: "center",
        width: 500
      },
      {
        title: "Action",
        align: "center",
        width: 160,
        render: (text, record, index) => <div>
          <Button background="update" mr={10} buttonContent={<Link to={{pathname: "./update", query: record}}>修改</Link>} />
          <Button background="danger" onClick={() => this.deleteAdvertise(record._id, index)} buttonContent="删除" />
        </div>
      }
    ];
    return (
    	<div>
        <BreadCrumb />
        <div className="container">
          <Button size="default" background="success" mb={20} buttonContent={<Link to="./create">添加素材</Link>} />
          <Table rowKey={record => record._id} size="small" bordered columns={advertiseHeader} dataSource={advertiseList} />
        </div>
    	</div>
    )
  }
  componentDidMount() {
    this.getAdvertiseList()
  }

  getAdvertiseList() {
    axios.get("/adMaterial").then(res => {
      if(res.data.code === 1) {
        let advertiseList = res.data.data.reverse();
        this.setState({
          advertiseList
        })
      }
    });
  }

  deleteAdvertise(id, index) {
    const { advertiseList } = this.state;
    Modal.confirm({
      title: "确定删除这条素材吗？",
      onOk: () => {
        axios.delete('/adMaterial', {params: {id}})
          .then(res => {
            message.info(res.data.msg);
            advertiseList.splice(index, 1)
            this.setState({
              advertiseList
            })
          });
      }
    })
  }
}

export default Advertise;