import React, {Component, Fragment} from 'react';
import BreadCrumb from "../../../../common/bread";
import {connect} from "react-redux";
import {Form, Button, Input, Radio, Icon, Upload, Modal, Checkbox} from "antd";
import * as actionCreators from '../store/actionCreators';
import { Editor } from '@tinymce/tinymce-react';

class EditNovelInfo extends Component {

  componentDidMount() {
    let query = this.props.history.location.query;
    query && this.props.initNovelInfo(query);
    query && this.props.getContent(query._id);
  }

  state = {
    deleteModalStyle: {position: "absolute", top: 0, left: 0, width: 100, height: 100, background: "rgba(0, 0, 0, .3)", color: "#fff", fontSize: 20, display: "none", boxSizing: "border-box"}
  };

  enterImg = e => {
    e.stopPropagation();
    this.setState(state => ({
      deleteModalStyle: {...state.deleteModalStyle, display: "block"}
    }))
  };

  leaveImg = e => {
    e.stopPropagation();
    this.setState(state => ({
      deleteModalStyle: {...state.deleteModalStyle, display: "none"}
    }))
  };

  render() {
    const {novelInfo, channelList, editChannelModal, selectChannelList, uploadImg, handleInputChange, submitNovel, cancel, selectEditChannel, cancelSelectEditChannel, changeEditChannel, openEditChannelModal, uploadFinalImg, uploadGonghaoLogo, deleteFinalImg } = this.props;
    const { deleteModalStyle } = this.state;
    const formItemLayout = {
      labelCol: {
        sm: {span: 6}
      },
      wrapperCol: {
        sm: {span: 12, offset: 0}
      },
    };
    let isUploadImg;
    if (novelInfo.picurl) {
      isUploadImg = <img style={{width: "100px"}} src={novelInfo.imgUrl} alt=""/>
    } else {
      isUploadImg = <Button><Icon type="upload"/> 上传图片 </Button>
    }
    let isWechat;
    if (novelInfo.type === 0) {
      isWechat = <Form.Item label="跳转链接">
        <Input.TextArea placeholder="请输入跳转链接" value={novelInfo.linkUrl}
                        onChange={e => handleInputChange(e.target.value, "linkUrl")}/>
      </Form.Item>
    }
    let isToutiao, isFinalUploadImg, notToutiao, isGonghaoUploadLogo;
    if (novelInfo.finalImg) {
      isFinalUploadImg = <div style={{width: 100, height: 100, position: "relative"}} onMouseOver={this.enterImg} onMouseLeave={this.leaveImg} onClick={deleteFinalImg}>
        <img style={{width: 100, height: 100}} src={novelInfo.finalImgUrl} alt=""/>
        <div className="cursor" style={deleteModalStyle}>
          <Icon style={{margin: "40px 0 0 40px"}} type="delete" />
        </div>
      </div>
    } else {
      isFinalUploadImg = <Upload name="imageFile" action="/tuiguang/novel/upload" onChange={uploadFinalImg} showUploadList={false}>
        <Button><Icon type="upload"/> 上传图片 </Button>
      </Upload>
    }
    if (novelInfo.gonghaoLogo) {
      isGonghaoUploadLogo = <img style={{width: "100px"}} src={novelInfo.gonghaoLogoUrl} alt=""/>
    } else {
      isGonghaoUploadLogo = <Button><Icon type="upload"/> 上传图片 </Button>
    }
    if (novelInfo.type === 3) {
      isToutiao = <>
        <Form.Item label="公司名称">
          <Input placeholder="请输入公司名称" value={novelInfo.company}
                 onChange={e => handleInputChange(e.target.value, "company")}/>
        </Form.Item>
        <Form.Item label="公号logo">
          <Upload name="imageFile" action="/tuiguang/novel/upload" onChange={uploadGonghaoLogo} showUploadList={false}>
            {isGonghaoUploadLogo}
          </Upload>
        </Form.Item>
        <Form.Item label="尾图图片">
          {isFinalUploadImg}
        </Form.Item>
      </>
    }
    if (novelInfo.type === 0 || novelInfo.type === 1) {
      notToutiao = <Fragment>
        <Form.Item label="文章标题">
          <Input placeholder="请输入文章标题" value={novelInfo.articleTit}
                 onChange={e => handleInputChange(e.target.value, "articleTit")}/>
        </Form.Item>

        <Form.Item label="公号描述">
          <Input placeholder="请输入公号描述" value={novelInfo.title}
                 onChange={e => handleInputChange(e.target.value, "title")}/>
        </Form.Item>
      </Fragment>
    }
    const channelWrapper = channelList.map(item => <Checkbox style={{margin: "0 10px 0 0"}} key={item._id}
                                                             value={item.name}>{item.name}</Checkbox>)
    return (
      <div>
        <BreadCrumb/>
        <div className='container'>
          <div className="title-wrapper">
            <h2>编辑小说内容</h2>
            <div className="fr">
              <Button type="primary" style={{marginRight: 10}}
                      onClick={() => submitNovel(novelInfo, this.props)}>提交</Button>
              <Button onClick={() => cancel(this.props)}>取消</Button>
            </div>
          </div>
          <Form className="fl" style={{width: "50%"}} {...formItemLayout}>
            <Form.Item label="类型">
              <Radio.Group value={novelInfo.type} onChange={e => handleInputChange(e.target.value, "type")}>
                <Radio value={0}>微跳</Radio>
                <Radio value={1}>非微调单页</Radio>
                <Radio value={3}>头条</Radio>
              </Radio.Group>
            </Form.Item>
            <Form.Item label="id">
              <Input placeholder="请输入id" value={novelInfo.id} onChange={e => handleInputChange(e.target.value, "id")}/>
            </Form.Item>
            <Form.Item label="公号名称">
              <Input placeholder="请输入公号名称" value={novelInfo.name}
                     onChange={e => handleInputChange(e.target.value, "name")}/>
            </Form.Item>
            <Form.Item label="域名">
              <Input placeholder="请输入域名" value={novelInfo.domain_name} onChange={e => handleInputChange(e.target.value, "domain_name")}/>
            </Form.Item>
            {notToutiao}
            <Form.Item label="统计代码">
              <Input.TextArea placeholder="请输入统计代码" value={novelInfo.statisticsUrl1}
                              onChange={e => handleInputChange(e.target.value, "statisticsUrl1")}/>
            </Form.Item>
            <Form.Item label="其他代码/统计id">
              <Input.TextArea placeholder="请输入其他业务代码或统计id" value={novelInfo.tokenCodes}
                              onChange={e => handleInputChange(e.target.value, "tokenCodes")}/>
            </Form.Item>
            <Form.Item label="备注">
              <Input placeholder="请输入备注名称" value={novelInfo.remarks}
                     onChange={e => handleInputChange(e.target.value, "remarks")}/>
            </Form.Item>
          </Form>
          <Form className="fl" style={{width: "50%", marginTop: 60}} {...formItemLayout}>
            <Form.Item label="页面标题">
              <Input placeholder="请输入页面标题" value={novelInfo.pageTitle}
                     onChange={e => handleInputChange(e.target.value, "pageTitle")}/>
            </Form.Item>
            <Form.Item label="图片">
              <Upload name="imageFile" action="/tuiguang/novel/upload" onChange={uploadImg} showUploadList={false}>
                {isUploadImg}
              </Upload>
            </Form.Item>
            {isToutiao}
            {isWechat}
            <Form.Item label="选择渠道">
              <Button onClick={openEditChannelModal}>{novelInfo.channel ? novelInfo.channel : "选择渠道"}</Button>
            </Form.Item>

          </Form>
          <Form style={{width: "100%", marginTop: 60, clear: "both"}} labelCol={{span: 3}} wrapperCol={{span: 20, offset: 0}}>
            <Form.Item label="文章内容">
              <Editor
                apiKey="0716d4da8zojzntbxderqtt6hahy4pg7832a1gj024wxtzdp"
                initialValue={novelInfo.capter1}
                init={{
                  menubar: false,
                  plugins: [
                    'tinydrive image code'
                  ],
                  toolbar: 'undo redo | bold italic | alignleft aligncenter | image code',
                  min_height:500
                }}
                onChange={e => handleInputChange(e.target.getContent(), "capter1")}
              />
              {/*<Input.TextArea placeholder="请输入文章内容" value={novelInfo.capter1}*/}
              {/*onChange={e => handleInputChange(e.target.value, "capter1")}/>*/}
            </Form.Item>
          </Form>
          <Modal
            visible={editChannelModal}
            title="请选择渠道"
            onOk={selectEditChannel}
            onCancel={cancelSelectEditChannel}
          >
            <Checkbox.Group
              value={selectChannelList}
              onChange={changeEditChannel}
            >
              {channelWrapper}
            </Checkbox.Group>
          </Modal>
        </div>
      </div>
    )
  }

  componentWillUnmount() {
    this.props.resetState()
  }
}

const mapState = state => ({
  novelInfo: state.novel.links.novelInfo,
  channelList: state.novel.links.channelList,
  editChannelModal: state.novel.links.editChannelModal,
  selectChannelList: state.novel.links.selectChannelList,
});

const mapDispatch = dispatch => ({
  uploadImg(info) {
    if (info.file.status === 'done') {
      let picurl = `/images/website/${info.file.response.filename}`;
      dispatch(actionCreators.handleInputChange(picurl, "picurl"));
    }
  },
  uploadFinalImg(info) {
    if (info.file.status === 'done') {
      let finalImg = `/images/website/${info.file.response.filename}`;
      dispatch(actionCreators.handleInputChange(finalImg, "finalImg"));
    }
  },
  uploadGonghaoLogo(info) {
    if (info.file.status === 'done') {
      let gonghaoLogo = `/images/website/${info.file.response.filename}`;
      dispatch(actionCreators.handleInputChange(gonghaoLogo, "gonghaoLogo"));
    }
  },
  handleInputChange(value, name) {
    dispatch(actionCreators.handleInputChange(value, name))
  },
  cancel(props) {
    props.history.goBack();
    dispatch(actionCreators.resetState())
  },
  submitNovel(novelInfo, props) {
    dispatch(actionCreators.submitNovel(novelInfo, props))
  },
  initNovelInfo(record) {
    dispatch(actionCreators.initNovelInfo(record))
  },
  changeEditChannel(checkedList) {
    dispatch(actionCreators.changeEditChannel(checkedList))
  },
  openEditChannelModal() {
    dispatch(actionCreators.openEditChannelModal())
  },
  cancelSelectEditChannel() {
    dispatch(actionCreators.cancelSelectEditChannel())
  },
  selectEditChannel() {
    dispatch(actionCreators.selectEditChannel())
  },
  getContent(_id) {
    dispatch(actionCreators.getContent(_id))
  },
  resetState() {
    dispatch(actionCreators.resetState())
  },
  deleteFinalImg() {
    dispatch(actionCreators.deleteFinalImg())
  }
});

export default connect(mapState, mapDispatch)(Form.create({name: 'editNovelInfo'})(EditNovelInfo));