import React, { Component } from 'react';
import BreadCrumb from '../../../common/bread';
import { Input, Table, Row, Col, message, Modal } from 'antd';
import axios from 'axios';
import Button from '../../../common/components/Button';

class ChannelManage extends Component {
  constructor(props) {
    super(props);
    this.changeChannelInput = this.changeChannelInput.bind(this);
    this.createChannel = this.createChannel.bind(this);
    this.cancelUpdate = this.cancelUpdate.bind(this);
  }
  state = {
    channelList: [],
    channel: "",
    index: -1,
    channelInput: ""
  };
  render() {
    const { channelList, channel, index, channelInput } = this.state;
    const channelHeader = [
      {
        key: 'id',
        dataIndex: 'id',
        title: 'id',
        align: "center"
      }, {
        key: "name",
        dataIndex: "name",
        title: "渠道名称",
        align: "center",
        width: 500,
        render: (text, record, i) => {
          let isUpdate = index === i;
          if (isUpdate) {
            return (
              <div>
                <Input size="small" style={{width: 200, marginRight: 10}} value={channelInput} onChange={(e) => this.changeUpdateInput(e.target.value)}/>
                <Button onClick={() => this.submitUpdate(record._id)} buttonContent="确定" />
                <Button background="info" onClick={this.cancelUpdate} buttonContent="取消" />
              </div>
            )
          } else {
            return <span>{text}</span>
          }
        }
      }, {
        key: "Action",
        title: "ACtion",
        align: "center",
        width: 300,
        render: (text, record, index) => <div>
          <Button mr={10} onClick={() => this.changeUpdateState(index, record)} buttonContent="编辑" />
          <Button background="danger" onClick={() => this.deleteChannel(record._id, index)} buttonContent="删除" />
        </div>
      }
    ];
    return (
      <div>
        <BreadCrumb />
        <div className="container">
          <Row style={{marginBottom: 20}}>
            <Col span={5}>
              <Input placeholder="请输入渠道名称" value={channel} onChange={this.changeChannelInput} />
            </Col>
            <Col span={4}>
              <Button onClick={this.createChannel} buttonContent="创建" size="default" />
            </Col>
          </Row>
          <Table size="small" rowKey={record => record._id} columns={channelHeader} dataSource={channelList} />
        </div>
      </div>
    )
  }

  componentDidMount() {
    this.getChannelList();
  }

  getChannelList() {
    axios.get("/channel")
      .then(res => {
        if(res.data.success) {
          let channelList = res.data.data.reverse();
          this.setState({
            channelList
          })
        }
      })
  }

  changeChannelInput(e) {
    let channel = e.target.value;
    this.setState({
      channel
    })
  }

  createChannel() {
    const { channel } = this.state;
    if(!channel.trim()) {
      message.info("渠道名称不能为空")
    } else {
      Modal.confirm({
        title: "确定创建一个新渠道吗？",
        onOk: () => {
          axios.post("/channel", {name: channel.trim()})
            .then(res => {
              if(res.data.success) {
                const { data } = res.data;
                message.success(res.data.success);
                this.setState(state => ({
                  channelList: [data, ...state.channelList],
                  channel: ""
                }));
              } else {
                message.info(res.data.err);
              }
            })
        }
      })
    }
  }

  changeUpdateInput(channelInput) {
    console.log(channelInput)
    this.setState({
      channelInput
    })
  }

  changeUpdateState(index, record) {
    this.setState({
      channelInput: record.name,
      index
    })
  }

  submitUpdate(_id) {
    const { channelInput, index, channelList } = this.state;
    if(!channelInput.trim()) {
      message.error("渠道名称不能为空")
    } else {
      Modal.confirm({
        title: "确认提交修改吗？",
        onOk: () => {
          axios.put("/channel", {name: channelInput, _id})
            .then(res => {
              if(res.data.success) {
                message.success(res.data.success);
                channelList.splice(index, 1, res.data.data);
                this.setState({
                  channelList
                });
                this.cancelUpdate()
              } else {
                message.info(res.data.err)
              }
            });
        }
      })
    }
  }

  deleteChannel(id, index) {
    const { channelList } = this.state;
    Modal.confirm({
      title: "确定删除这个渠道吗？",
      onOk: () => {
        axios.delete("/channel", {params: {id}})
          .then(res => {
            message.success(res.data.success);
            channelList.splice(index, 1);
            this.setState({
              channelList
            })
          });
      }
    })
  }

  cancelUpdate() {
    this.setState({
      index: -1,
      channelInput: ""
    })
  }
}


export default ChannelManage;