import React, { Component } from 'react';
import BreadCrumb from '../../../../common/bread';
import { Form, Input, Table, Button, message, Modal } from 'antd';
import axios from 'axios';

const initState = {
  imgList: [],
  advertiseInfo: {
    url: "",
    weight: "",
    sign: ""
  },
  isUpdate: false,
  index: null,
  isSubmit: false,
  title: "",
  novelLink: ""
};

class EditAdvertise extends Component {
  constructor(props) {
    super(props);
    this.add = this.add.bind(this);
    this.save = this.save.bind(this);
    this.cancelEdit = this.cancelEdit.bind(this);
    this.submitAdvertise = this.submitAdvertise.bind(this);
    this.changeTitleInput = this.changeTitleInput.bind(this);
    this.openAdvertiseModal = this.openAdvertiseModal.bind(this);
    this.closeAdvertiseModal = this.closeAdvertiseModal.bind(this);
    this.changeNovelLinkInput = this.changeNovelLinkInput.bind(this);
  }
  state = {...initState};

  componentWillMount() {
    let query = this.props.history.location.query;
    query && this.initData(query);
  }

  render() {
    const { advertiseInfo, imgList, isUpdate, isSubmit, title, novelLink } = this.state;
    const adImgHeader = [
      {
        key: 'url',
        dataIndex: 'url',
        title: '图片地址',
        align: 'center',
        width: 300
      }, {
        key: 'sign',
        dataIndex: 'sign',
        title: '标识',
        align: 'center'
      }, {
        key: 'weight',
        dataIndex: 'weight',
        title: '权重',
        align: 'center'
      }, {
        title: 'Action',
        align: 'center',
        width: 80,
        render: (text, record, index) => <div>
          <Button style={{marginBottom: 10}} size="small" type="primary" onClick={() => this.updateAdvertise(record, index)}>修改</Button>
          <Button size="small" type="danger" onClick={() => this.deleteAdvertise(index)}>删除</Button>
        </div>
      }
    ];
    let AddOrSave;
    if(!isUpdate) {
      AddOrSave = <Button onClick={this.add}>添加</Button>
    } else {
      AddOrSave = <Button onClick={this.save}>保存</Button>
    }
    return (
    	<div>
        <BreadCrumb />
        <div className="container">
          <div className="title-wrapper">
            <h2>编辑广告素材</h2>
            <div className="fr">
              <Button type="primary" style={{marginRight: 10}} onClick={this.openAdvertiseModal}>提交</Button>
              <Button onClick={this.cancelEdit}>取消</Button>
            </div>
          </div>
          <Form className="fl" style={{width: "40%"}} labelCol={{sm: 4}} wrapperCol={{sm: 18, offset: 0}}>
            <Form.Item label="图片地址">
              <Input.TextArea value={advertiseInfo.url} placeholder="请输入图片地址" onChange={e => this.changeInputValue(e.target.value, "url")} />
            </Form.Item>
            <Form.Item label="标识">
              <Input value={advertiseInfo.sign} placeholder="请输入标识" onChange={e => this.changeInputValue(e.target.value, "sign")} />
            </Form.Item>
            <Form.Item label="权重">
              <Input value={advertiseInfo.weight} placeholder="请输入权重" onChange={e => this.changeInputValue(e.target.value, "weight")} />
            </Form.Item>
            <Form.Item label=" " colon={false}>
              {AddOrSave}
            </Form.Item>
          </Form>
          <Table className="fl" style={{width: "60%"}} rowKey={(record, index) => index} size="small" bordered dataSource={imgList} columns={adImgHeader} />
        </div>
        <Modal
          visible={isSubmit}
          onOk={this.submitAdvertise}
          onCancel={this.closeAdvertiseModal}
          cancelText="取消"
          okText="确定"
        >
          <Form labelCol={{sm: 4}} wrapperCol={{sm: 18, offset: 0}}>
            <Form.Item label="标题">
              <Input value={title} placeholder="请输入素材标题" onChange={this.changeTitleInput}/>
            </Form.Item>
            <Form.Item label="小说链接">
              <Input value={novelLink} placeholder="请输入小说链接" onChange={this.changeNovelLinkInput}/>
            </Form.Item>
          </Form>
        </Modal>
    	</div>
    )
  }

  initData(query) {
    this.setState(state => ({...state, ...query}))
  }

  changeInputValue(value, name) {
    let { advertiseInfo } = this.state;
    advertiseInfo[name] = value.trim();
    this.setState({advertiseInfo})
  }

  changeTitleInput(e) {
    this.setState({
      title: e.target.value.trim()
    })
  }

  changeNovelLinkInput(e) {
    this.setState({
      novelLink: e.target.value.trim()
    })
  }

  add() {
    const { advertiseInfo, imgList } = this.state;
    if ( !advertiseInfo.url || !advertiseInfo.weight || !advertiseInfo.sign) {
      message.info("输入有误，请检查");
    } else {
      imgList.push({...advertiseInfo});
      this.setState({
        imgList,
        advertiseInfo: {
          url: "",
          weight: "",
          sign: ""
        }
      })
    }
  }
  save() {
    const { advertiseInfo, imgList, index } = this.state;
    if ( !advertiseInfo.url || !advertiseInfo.weight || !advertiseInfo.sign) {
      message.info("输入有误，请检查");
    } else {
      imgList.splice(index, 1, {...advertiseInfo});
      this.setState({
        imgList,
        isUpdate: false,
        advertiseInfo: {
          url: "",
          weight: "",
          sign: ""
        }
      })
    }
  }

  updateAdvertise(record, index) {
    this.setState({
      advertiseInfo: {...record},
      index,
      isUpdate: true
    })
  }

  deleteAdvertise(index) {
    let { imgList } = this.state;
    imgList.splice(index, 1)
    this.setState({
      imgList
    })
  }

  submitAdvertise() {
    const { imgList, title, novelLink, _id } = this.state;
    let query = this.props.history.location.query;
    if(!query) {
      axios.post('/adMaterial', {imgList, title, novelLink})
        .then(res => {
          if (res.data.code === 1) {
            message.info(res.data.msg);
            this.cancelEdit()
          } else {
            message.info(res.data.msg);
          }
        });
    } else {
      axios.put('/adMaterial', {imgList, title, novelLink, _id})
        .then(res => {
          if (res.data.code === 1) {
            message.info(res.data.msg);
            this.cancelEdit()
          } else {
            message.info(res.data.msg);
          }
        });
    }

  }

  closeAdvertiseModal() {
    this.setState({
      isSubmit: false
    })
  }

  openAdvertiseModal() {
    this.setState({
      isSubmit: true
    })
  }

  cancelEdit() {
    this.resetState();
    this.props.history.goBack();
  }

  resetState() {
    this.setState({...initState})
  }

  componentWillUnmount() {
    this.resetState()
  }
}

export default EditAdvertise;