import React, { Component } from 'react';
import { Input, Button, Table, Modal, message } from 'antd';
import axios from 'axios';
import BreadCrumb from '../../../common/bread';

class Tag extends Component {
  constructor(props) {
    super(props);
    this.createTag = this.createTag.bind(this);
  }
  state = {
    tag: "",
    tagList: []
  };
  render() {
    const { tag, tagList } = this.state;
    const tagHeader = [
      {
        key: "_id",
        dataIndex: "_id",
        title: "序号",
        align: "center"
      },
      {
        key: "name",
        dataIndex: "name",
        title: "标签名称",
        align: "center"
      },
      {
        title: "Action",
        align: "center",
        render: (text, record, index) => <Button size="small" type="danger" onClick={() => this.deleteTag(record._id, index)}>删除</Button>
      }
    ];
    return (
    	<div>
        <BreadCrumb />
        <div className="container">
          <Input.Search style={{width: 300, marginBottom: 20}} value={tag} onChange={e => this.setState({tag: e.target.value})} enterButton="新增标签" onSearch={this.createTag} />
          <Table rowKey={record => record._id} size="small" bordered columns={tagHeader} dataSource={tagList} />
        </div>
    	</div>
    )
  }

  componentDidMount() {
    this.getTagList();
  }

  getTagList() {
    axios.get("/tag").then(res => {
      const { data: tagList } = res.data;
      this.setState({tagList})
    });
  }

  createTag() {
    const { tag, tagList } = this.state;
    Modal.confirm({
      title: "确定创建一个新标签吗？",
      onOk: () => {
        axios.post("/tag", { name: tag }).then(res => {
          message.info("添加成功");;
          tagList.push(res.data.data)
          this.setState({tagList});
        });
      }
    })
  }

  deleteTag(id, index) {
    const { tagList } = this.state;
    Modal.confirm({
      title: "确认删除这条内容吗？",
      onOk: () => {
        axios.delete("/tag/" + id).then(res => {
          message.info("删除成功");
          tagList.splice(index, 1);
          this.setState({tagList});
        });
      }
    })
  }
}

export default Tag;