import React, { Component } from 'react';
import BreadCrumb from '../../../common/bread'
import {Table, Modal, Button, message, Radio, Input, Form} from 'antd';
import CopyToClipboard from 'react-copy-to-clipboard';
import { connect } from 'react-redux';
import { actionCreators } from './store';
import { Link }from 'react-router-dom';

class Recommend extends Component {

  render() {
    const { domain_name, recommendList, recommendModal, recommendInfo, deleteItem, openRecommendModal, createRecommendList, closeRecommendModal } = this.props;
    const recommendHeader = [
      {
        dataIndex: 'id',
        key: 'id',
        title: 'ID',
        align: 'center'
      }, {
        dataIndex: 'type',
        title: '类型',
        align: 'center',
        render: text => text === 0 ? '微信内' : '微信外'
      }, {
        title: '复制链接',
        align: 'center',
        render: (text, record) => <CopyToClipboard text={domain_name +
        "/recommend/list/" + record.id} onCopy={() => message.success("复制成功")}>
          <Button size="small" style={{background: "lightblue", color: "#fff"}}>复制</Button>
        </CopyToClipboard>
      }, {
        title: 'Action',
        align: 'center',
        width: 200,
        render: (text, record, index) => <div>
          <Button size="small" type="primary" style={{marginRight: 10}}>
            <Link to={{pathname: "./recommendList/" + JSON.stringify(record)}}>修改</Link>
          </Button>
          <Button size="small" type="danger" onClick={() => deleteItem(record._id, index)}>删除</Button>
        </div>
      }
    ];
    return (
    	<div>
        <BreadCrumb />
        <div className="container">
          <Button style={{marginBottom: 20}} type="primary" onClick={openRecommendModal}>新增返回小说列表</Button>
          <Table size="small" bordered rowKey={record => record._id} columns={recommendHeader} dataSource={recommendList} />
        </div>
        <Modal
          visible={recommendModal}
          onOk={() => createRecommendList(recommendInfo, this.props)}
          onCancel={closeRecommendModal}
        >
          <Form labelCol={{sm: {span: 4}}} wrapperCol={{sm: {span: 12, offset: 0}}}>
            <Form.Item label="类型">
              <Radio.Group id="type" value={recommendInfo.type}>
                <Radio value="0">微信内</Radio>
                <Radio value="1">微信外</Radio>
              </Radio.Group>
            </Form.Item>
            <Form.Item label="统计代码">
              <Input.TextArea placeholder="——选填——" id="listCode" value={recommendInfo.listCode}/>
            </Form.Item>
          </Form>
        </Modal>
    	</div>
    )
  }

  componentDidMount() {
    this.props.getDomainName();
    this.props.getRecommendList();
  }
}

const mapState = state => ({
  recommendList: state.novel.recommend.recommendList,
  domain_name: state.novel.recommend.domain_name,
  recommendModal: state.novel.recommend.recommendModal,
  recommendInfo: state.novel.recommend.recommendInfo,
});

const mapDispatch = dispatch => ({
  getDomainName() {
    dispatch(actionCreators.getDomainName())
  },
  getRecommendList() {
    dispatch(actionCreators.getRecommendList())
  },
  deleteItem(id, index) {
    dispatch(actionCreators.deleteItem(id, index))
  },
  openRecommendModal() {
    dispatch(actionCreators.openRecommendModal())
  },
  createRecommendList(recommendInfo, props) {
    dispatch(actionCreators.createRecommendList(recommendInfo, props))
  },
  closeRecommendModal() {
    dispatch(actionCreators.closeRecommendModal())
  }
});

export default connect(mapState, mapDispatch)(Recommend);