import axios from 'axios';
import * as constants from './constants';
import { Modal, message } from 'antd';
// import backTop from "../../../../utils";

export const getDomainName = () => {
  return dispatch => {
    axios.get("/tuiguang/novel/show").then(res => {
      let domain_name = res.data.domain_names[0].domain_name;
      const action = {
        type: constants.GET_DOMAIN_NAME,
        domain_name
      };
      dispatch(action)
    });
  }
};

const getData = () => {
  return new Promise((resolve, reject) => {
    axios.get("/recommend/list/show")
      .then(res => {
        if (res.data.data.length > 0) {
          let recommendList = res.data.data;
          const action = {
            type: constants.GET_RECOMMEND_LIST,
            recommendList
          };
          resolve(action)
        } else {
          reject(null)
        }
      })
  });
};

export const getRecommendList = () => {
  return async dispatch => {
    let action = await getData();
    dispatch(action);
  }
};

export const deleteItem = (id, index) => {
  return dispatch => {
    Modal.confirm({
      title: "确认删除这小返回链接吗？",
      onOk: () => {
        axios.get('/recommend/list/del', {params: {id}})
          .then(res => {
            message.info('删除成功')
            const action = {
              type: constants.DELETE_ITEM,
              index
            };
            dispatch(action);
          })
      }
    })
  }
};

export const openRecommendModal = () => ({
  type: constants.OPEN_RECOMMEND_MODAL
});

export const closeRecommendModal = () => ({
  type: constants.CLOSE_RECOMMEND_MODAL
});

export const createRecommendList = (recommendInfo, props) => {
  return dispatch => {
    Modal.confirm({
      title: "确定创建一条新的推荐链接吗？",
      onOk: () => {
        axios.post('/recommend/list/create', {...recommendInfo})
          .then(res => {
            if(res.data.success) {
              const { data } = res.data;
              message.info(res.data.success);
              dispatch(closeRecommendModal());
              props.history.push('./recommendList/' + JSON.stringify({...data}));
              dispatch({
                type: constants.CREATE_RECOMMEND_LIST,
                data
              })
            }
          })
      }
    })
  }
};



export const resetState = () => ({
  type: constants.RESET_STATE
});
