import axios from 'axios';
import * as constants from './constants';
import { Modal, message } from 'antd';
import backTop from "../../../../utils";

// 群发公号
const getData = (page, channel) => {
  return new Promise((resolve, reject) => {
    axios.get("/tuiguang/novel/show",{params: {page: page || 1, channel: channel || ''}})
      .then(res => {
        if (res.data.data.length > 0) {
          let novelLinkList = res.data.data, domain_names = res.data.domain_names, count = res.data.count;
          const action = {
            type: constants.GET_NOVEL_LINK_LIST,
            novelLinkList, domain_names, count
          };
          resolve(action)
        } else {
          reject(null)
        }
      })
  });
};

export const getNovelLinkList = () => {
  return async dispatch => {
    let action = await getData();
    dispatch(action);
  }
};

export const changePage = (page) => {
  return async dispatch => {
    dispatch(await getData(page));
    const action = {
      type: constants.CHANGE_PAGE,
      page
    };
    await backTop();
    await dispatch(action);
  }
};

export const deleteNovel = (id, index) => {
  return dispatch => {
    Modal.confirm({
      title: "确定删除这条链接吗？",
      onOk: () => {
        axios.post("/tuiguang/novel/delete_one", {id: id})
          .then(res => {
            if(res.data.message) {
              message.info(res.data.message);
              const action = {
                type: constants.DELETE_NOVEL,
                index
              };
              dispatch(action);
            }
          });
      }
    })
  }
};

export const getChannelList = () => {
  return dispatch => {
    axios.get('/channel')
      .then(res => {
        if(res.data.success) {
          const channelList = res.data.data;
          const action = {
            type: constants.GET_CHANNEL_LIST,
            channelList
          };
          dispatch(action);
        }
      })
  }
};

export const openChannelModal = () => ({
  type: constants.OPEN_CHANNEL_MODAL
});

export const cancelSelectChannel = () => ({
  type: constants.CANCEL_SELECT_CHANNEL
});

export const changeChannel = channel => ({
  type: constants.CHANGE_CHANNEL,
  channel
});

export const selectChannel = channel => {
  return async dispatch => {
    channel === "默认" && (channel = "");
    dispatch(await getData(null, channel));
    dispatch({type: constants.SELECT_CHANNEL});
    dispatch(await cancelSelectChannel())
  }
};

export const openDomainModal = () => ({
  type: constants.OPEN_DOMAIN_MODAL
});

export const closeDomainModal = () => ({
  type: constants.CLOSE_DOMAIN_MODAL
});
export const changeDomainName = domain => ({
  type: constants.CHANGE_DOMAIN_INPUT,
  domain
});

export const submitDomain = domain_name => {
  return dispatch => {
    if (!domain_name) {
      message.info("域名不能为空，请重新填写！！！");
    } else {
      axios.get('/transfer/update_links', {
        params: {
          id: "5b6d0b899a9fab38f48b5b10",
          domain_name
        }
      }).then(res => {
        message.info(res.data.success);
        dispatch(closeDomainModal())
      });
    }
  }
};

// 编辑小说链接详情

export const submitNovel = (record, props) => {
  return dispatch => {
    let query = props.history.location.query;
    let title = query ? "确认保存修改吗？" : "确认创建一条新的小说链接吗？";
    let url = query ? "/tuiguang/novel/update" : "/tuiguang/novel/add";
    let tips = query ? "保存成功": "创建成功";
    Modal.confirm({
      title: title,
      onOk: () => {
        axios.post(url, {...record}).then(async res => {
          if (res.data.err) {
            message.info(res.data.err);
          } else {
            if((!query && !record._id) || (record._id && query)) {
              props.history.goBack();
            }
            message.success(tips);
            dispatch(await getData());
          }
        });
      }
    })
  }
};

