export const getContent = _id => {
  return dispatch => {
    axios.get("/tuiguang/novel/get_content", { params: { _id } })
      .then(res => {
        const { data } = res.data;
        const action = {
          type: constants.GET_CONTENT,
          data
        };
        dispatch(action);
      });
  }
};


