import * as constants from './constants';

const defaultNovelInfo = {
  type: 3,
  id: "",
  pageTitle: "",
  articleTit: "",
  name: "",
  title: "",
  picurl: "",
  capter1: "",
  linkUrl: "",
  statisticsUrl1: "",
  tokenCodes: "",
  channel: "",
  remarks: "",
  imgUrl: "",
  finalImg: "",
  finalImgUrl: "",
  gonghaoLogo: "",
  gonghaoLogoUrl: "",
  domain_name: "http://novel.jtjsmp.top",
  company: ""
};

const defaultState = {
  novelLinkList: [],
  domain_name: "",
  domainModal: false,
  count: null,
  currentPage: 1,
  channelModal: false,
  channel: "默认",
  selectedChannel: "默认",
  channelList: [],
  novelInfo: defaultNovelInfo,
  editChannelModal: false,
  selectChannelList: [],
  domain: ""
};

export default (state = defaultState, action) => {
  let newState = JSON.parse(JSON.stringify(state));
  switch (action.type) {
    case constants.GET_NOVEL_LINK_LIST:
      newState.novelLinkList = [...action.novelLinkList];
      newState.domain_name = action.domain_names[0].domain_name;
      newState.count = action.count;
      return newState;
    case constants.CHANGE_PAGE:
      newState.currentPage = action.page;
      return newState;
    case constants.DELETE_NOVEL:
      newState.novelLinkList.splice(action.index, 1);
      return newState;
    case constants.GET_CHANNEL_LIST:
      newState.channelList = [...action.channelList];
      return newState;
    case  constants.OPEN_CHANNEL_MODAL:
      newState.channelModal = true;
      return newState;
    case  constants.CANCEL_SELECT_CHANNEL:
      newState.channelModal = false;
      newState.channel = "默认";
      return newState;
    case constants.CHANGE_CHANNEL:
      newState.channel = action.channel;
      return newState;
    case constants.SELECT_CHANNEL:
      newState.selectedChannel = newState.channel;
      return newState;
    case constants.OPEN_DOMAIN_MODAL:
      newState.domainModal = true;
      return newState;
    case constants.CLOSE_DOMAIN_MODAL:
      newState.domainModal = false;
      newState.domain = "";
      return newState;
    case constants.CHANGE_DOMAIN_INPUT:
      newState.domain = action.domain;
      return newState;

    // 编辑小说链接详情

    case constants.HANDLE_INPUT_CHANGE:
      newState.novelInfo[action.name] = action.value;
      if(action.name === "picurl") {
        newState.novelInfo.imgUrl = newState.domain_name + action.value;
      }
      if(action.name === "finalImg") {
        newState.novelInfo.finalImgUrl = newState.domain_name + action.value;
      }
      if(action.name === "gonghaoLogo") {
        newState.novelInfo.gonghaoLogoUrl = newState.domain_name + action.value;
      }
      return newState;
    case constants.OPEN_EDIT_CHANNEL_MODAL:
      newState.editChannelModal = true;
      return newState;
    case constants.CANCEL_SELECT_EDIT_CHANNEL:
      newState.editChannelModal = false;
      newState.selectChannelList = [];
      return newState;
    case constants.CHANGE_EDIT_CHANNEL:
      newState.selectChannelList = [...action.channelList];
      return newState;
    case constants.SELECT_EDIT_CHANNEL:
      newState.novelInfo.channel = newState.selectChannelList[0];
      return newState;
    case constants.INIT_NOVEL_INFO:
      newState.novelInfo = {...action.record};
      newState.novelInfo.imgUrl = newState.domain_name + action.record.picurl;
      newState.novelInfo.finalImgUrl = newState.domain_name + action.record.finalImg;
      newState.novelInfo.gonghaoLogoUrl = newState.domain_name + action.record.gonghaoLogo;
      return newState;
    case constants.GET_CONTENT:
      newState.novelInfo.capter1 = action.data.capter1;
      newState.novelInfo.capter2 = action.data.capter2;
      return newState;
    case constants.DELETE_FINAL_IMG:
      newState.novelInfo.finalImg = newState.novelInfo.finalImgUrl = "";
      return newState;

    case constants.RESET_STATE:
      newState = defaultState;
      return newState;
    default:
      return state;
  }
};