import React, { Component } from 'react';
import { Route, Redirect, Switch } from "react-router-dom";
import Links from "./links";
import EditLinks from "./links/edit";
import novelSpread from './novelSpread'
import Recommend from './recommend';
import NovelRecommend from './recommend/novel';

class NovelRouter extends Component {
  render() {
    return (
    	<div>
        <Route path='/admin/novel'>
          <Switch>
            <Route path="/admin/novel/novelLinks/index" component={Links} />
            <Route path="/admin/novel/novelLinks/create" component={EditLinks} />
            <Route path="/admin/novel/novelLinks/update" component={EditLinks} />
            <Route path="/admin/novel/novelSpread" component={novelSpread} />
            <Route path="/admin/novel/recommend/index" component={Recommend} />
            <Route path="/admin/novel/recommend/recommendList/:recommendInfo" component={NovelRecommend} />
            <Redirect from="/admin/novel" exact to='/admin/novel/novelLinks' />
            <Redirect from="/admin/novel/novelLinks" exact to='/admin/novel/novelLinks/index' />
            <Redirect from="/admin/novel/recommend" exact to='/admin/novel/recommend/index' />
          </Switch>
        </Route>
    	</div>
    )
  }
}

export default NovelRouter;