import React, { Component } from 'react';
import { Route, Switch, Redirect } from 'react-router-dom';
import QrCode from './qrcode';
import EditQrCode from './qrcode/edit';
import Tag from './tag';
import Qiangguan from './qiangguan';

class WeChatRouter extends Component {
  render() {
    return (
    	<Switch>
        <Route path="/admin/wechat/qrcode/index" component={QrCode} />
        <Route path="/admin/wechat/qrcode/create" component={EditQrCode} />
        <Route path="/admin/wechat/qrcode/update" component={EditQrCode} />
        <Route path="/admin/wechat/tag" component={Tag} />
        <Route path="/admin/wechat/qiangguan" component={Qiangguan} />
        <Redirect from="/admin/wechat" exact to="/admin/wechat/qrcode"/>
        <Redirect from="/admin/wechat/qrcode" to="/admin/wechat/qrcode/index"/>
    	</Switch>
    )
  }
}

export default WeChatRouter;